.productBlock{
    display: flex;
    flex-wrap: wrap;
}
.announceBlock, .aboutUsBlock{
    background: #fff;
    padding: 20px;
}
.announceBlock a{
    color: inherit;
    text-decoration: unset;
    display: block;
    font-size: 16px;
    border-bottom: 1px #e5e5e5 solid;
    padding: 0 10px 10px;
}
.announceBlock a ~ a{
    padding: 10px;
}
.announceBlock a:hover{
    color: #1f1f1f;
    font-weight: bold;
}
/* tmp  */
.aboutUsBlock{
    height: 300px;
    background: #fff;
}
.product{
    width: calc(100% / 3 - 20px);
    text-align: center;
    padding: 20px;
    background: #fff;
    box-sizing: border-box;
    cursor: pointer;
    margin: 15px 0 15px 30px;
    /* margin-left: 30px; */

}
.product:nth-child(3n+1){
    margin-left: 0;
}
.product img{
    width: 100%;
    height: auto;
    margin: 0 auto 20px;
}
.product p{
    font-size: 18px;
}

