.pageContainer{
  width: 100% !important;
  padding: 30px 0;
  margin: 0;
}
.page{
  max-width: 1000px;
  margin: 0 auto;
}
.block{
  margin: 0 auto 30px;
}

/* btn */
.btnBlock{
  float: right;
  margin-bottom: 30px;
}
.btn{
  border-radius: 0;
  font-size: 16px;
  background-color: #313131;
  border-color: #313131;
  color: #fff;
  /* font-weight: bold; */
}
.btn.focus, .btn:focus, .btn:hover {
  background-color: #1a1a1a;
  border-color: #1a1a1a;
  color: #fff;
  text-decoration: none;
}
.btn-danger:hover {
  color: #fff;
  background-color: #c9302c;
  border-color: #ac2925;
}
.btn-danger {
  color: #fff;
  background-color: #d9534f;
  border-color: #d43f3a;
}
.btnBlock .btn ~ .btn{
  margin-left: 20px;
}

/* bootstrap overwrite */
.navbar-default .navbar-nav>li>a:focus, .navbar-default .navbar-nav>li>a:hover{
  font-weight: 600;
}
.navbar-default .navbar-nav>.active>a, .navbar-default .navbar-nav>.active>a:focus, .navbar-default .navbar-nav>.active>a:hover {
  color: #555;
  background-color: inherit;
  font-weight: 600;

}
.form-control{
  width: auto !important;
}
